import React, {Component} from 'react'
import {connect} from 'react-redux'
import PropTypes from 'prop-types'

import * as vkSelectors from '../../store/vk/reducer'
import * as vkActions from '../../store/vk/actions'
import {
    Panel,
    PanelHeader,
    Group,
    List,
    Cell,
    InfoRow,
    Avatar
} from '@vkontakte/vkui'
import Footer from '../Footer'
import Logger from '../Loger'

export class ProfilePanel extends Component {
    componentWillMount() {}
    componentDidUpdate() {
        if (this.props.accessToken) {
            this
                .props
                .dispatch(vkActions.getUserInfo(this.props.accessToken))
        }
    }

    render() {
        const isProduction = process.env.NODE_ENV === 'production';
        let logger = null;
        if (!isProduction) {
            logger = <Logger/>;
        }
        //const {city} = this.props.userInfo

        return (
            <Panel id={this.props.id}>
                <PanelHeader>Профиль</PanelHeader>
                <Group title="Информация о пользователе">
                    <List>
                        <Cell>
                            <InfoRow title='Дата рождения'>{}</InfoRow>
                        </Cell>
                        <Cell>
                            <InfoRow title='Родной город'>{this.props.userInfo}</InfoRow>
                        </Cell>
                        <Cell >
                            <InfoRow title='Место работы'></InfoRow>
                        </Cell>
                    </List>
                </Group>
                <Footer/> {logger}
            </Panel>
        )
    }
}

ProfilePanel.propTypes = {
    id: PropTypes.string.isRequired,
    accessToken: PropTypes.string,
    userInfo: PropTypes.object
}

function mapStateToProps(state) {
    return {
        userInfo: vkSelectors.getUserInfo(state)
    }
}
export default connect(mapStateToProps)(ProfilePanel)





