import React, {Component} from 'react'
import {
    Panel,
    PanelHeader,
    List,
    ListItem,
    Group,
    HeaderButton,
    platform,
    IOS
} from '@vkontakte/vkui'
import { goBack } from 'react-router-redux';
import { connect } from 'react-redux'
import PropTypes from 'prop-types'

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

export class AboutProfile extends Component {
    render() {
        const osname = platform()
        return (
            <Panel id={this.props.id}>
                <PanelHeader left={<HeaderButton onClick={this.navigationBack.bind(this)}>{osname === IOS ?
                    <Icon28ChevronBack /> : <Icon24Back />}</HeaderButton>}
                    >
                    About
                </PanelHeader>
                <Group title="Items">
                    <List>
                        <ListItem>Привет</ListItem>
                        <ListItem>World</ListItem>
                    </List>
                </Group>
            </Panel>
        )
    }
    navigationBack(){
        this.props.dispatch(goBack())
    }
}

AboutProfile.propTypes = {
    id: PropTypes.string.isRequired,
}
function mapStateToProps (state){
    return{}
}
export default connect(mapStateToProps )(AboutProfile)


