import imutable from 'seamless-immutable'
import {
    VK_GET_USER_INFO_FETCHED,
    VK_GET_USER_INFO_FAILED,
    VK_NOTIFICATION_STATUS_FETCHED,
    VK_NOTIFICATION_STATUS_FAILED,
    VK_INSETS_FETCHED,
    VK_GET_ACCESS_TOKEN_FETCHED,
    VK_GET_ACCESS_TOKEN_FAILED
} from './actionsTypes'

const initialState = imutable({vk: undefined})

export const vk = (state = initialState, action) => {
    switch (action.type) {
        case VK_GET_ACCESS_TOKEN_FETCHED:
            return state.merge({accessToken: action.accessToken})
        case VK_GET_USER_INFO_FETCHED:
            return state.merge({userInfo: action.userInfo})
        case VK_NOTIFICATION_STATUS_FETCHED:
            return state.merge({notificationStatus: action.notificationStatus})
        case VK_INSETS_FETCHED:
            return state.merge({insets: action.insets})
        case VK_NOTIFICATION_STATUS_FAILED:
            return state.merge({logs: action.error})
        case VK_GET_ACCESS_TOKEN_FAILED:
            return state.merge({logs: action.error})
        case VK_GET_USER_INFO_FAILED:
            return state.merge({logs: action.error})
        default:
            return state
    }

}

export function getUserInfo(state) {
    return state.userInfo
}

export function getAccessToken(state) {
    return state.vk.accessToken;
}

export function getNotificationStatus(state) {
    return state.vk.notificationStatus;
}

export function getInsets(state) {
    return state.vk.insets;
}

export function getLogs(state) {
    return JSON.stringify(state.vk.logs);
}



