import VKConnect from '@vkontakte/vkui-connect';
import {
    VK_GET_USER_INFO_FETCHED,
    VK_GET_USER_INFO_FAILED,
    VK_NOTIFICATION_STATUS_FETCHED,
    VK_NOTIFICATION_STATUS_FAILED,
    VK_INSETS_FETCHED,
    VK_GET_ACCESS_TOKEN_FETCHED
} from './actionsTypes'

const API_VERSION = '5.80'

export function fetchAccessToken() {
    return async() => {
        let appId = process.env.NODE_ENV === 'production'
            ? 6625834
            : 6625863;
        VKConnect.send('VKWebAppGetAuthToken', {'app_id': appId});
    }
}

export function fetchNotificationStatus(accessToken) {
    return async(dispatch) => {
        apiRequest('apps.isNotificationsAllowed', {}, accessToken, response => {
            dispatch({type: VK_NOTIFICATION_STATUS_FETCHED, notificationStatus: response['is_allowed']})
        }, error => {
            dispatch({type: VK_NOTIFICATION_STATUS_FAILED, error: error})
        })
    }
}

export function denyNotifications() {
    return async(dispatch) => {
        apiRequest('VKWebAppDenyNotifications', {})
    }
}
export function allowNotifications() {
    return async(dispatch) => {
        apiRequest('VKWebAppAllowNotifications', {})
    }
}

export function getUserInfo() {
    return async() => {
        VKConnect.send('VKWebAppGetUserInfo', {})/*, accessToken, response => {
            dispatch({ type: VK_GET_USER_INFO_FETCHED, userInfo: response.data })
        }, error => {
            dispatch({ type: VK_GET_USER_INFO_FAILED, error: error })
        })*/
    }
}

export function initApplication() {
    return async(dispatch) => {
        VKConnect.subscribe(e => {
            let vkEvent = e.detail;
            if (!vkEvent) {
                console.error('invalid event', e);
                return;
            }

            let type = vkEvent['type'];
            let data = vkEvent['data'];

            switch (type) {
                case 'VKWebAppAllowNotificationsResult':
                    dispatch({type: VK_NOTIFICATION_STATUS_FETCHED, notificationStatus: true});
                    break;
                case 'VKWebAppDenyNotificationsResult':
                    dispatch({type: VK_NOTIFICATION_STATUS_FETCHED, notificationStatus: false});
                    break;
                case 'VKWebAppAccessTokenReceived':
                    dispatch({type: VK_GET_ACCESS_TOKEN_FETCHED, accessToken: data['access_token']});
                    break;
                case 'VKWebAppUpdateInsets':
                    dispatch({type: VK_INSETS_FETCHED, insets: data.insets});
                    break;
                case 'VKWebAppGetUserInfoResult':
                    dispatch({type: VK_GET_USER_INFO_FETCHED, userInfo: data});
                    break;
                case 'VKWebAppGetUserInfoFailed':
                    dispatch({type: VK_GET_USER_INFO_FAILED, error:data});
                    break;
                default:
            }

        })
        VKConnect.send('VKWebAppInit', {'no_toolbar': false});

    }
}

function apiRequest(method, params = {}, accessToken = '', successCallback = undefined, errorCallback = undefined) {
    let requestId = getNewRequestId();
    if (successCallback !== undefined || errorCallback !== undefined) {
        let clb = function callback(e) {
            let vkEvent = e.detail;
            if (!vkEvent) {
                console.error('invalid event', e);
                return;
            }

            let type = vkEvent['type'];
            let data = vkEvent['data'];

            let found = false;
            if ('VKWebAppCallAPIMethodResult' === type && data['request_id'] === requestId) {
                if (successCallback !== undefined) {
                    successCallback(data['response']);
                }

                found = true;
            } else if ('VKWebAppCallAPIMethodFailed' === type && data['request_id'] === requestId) {
                if (errorCallback !== undefined) {
                    errorCallback(data);
                }

                found = true;
            }

            if (found) {
                VKConnect.unsubscribe(clb);
            }

        };

        VKConnect.subscribe(clb);
    }

    params['access_token'] = accessToken;

    if (params['v'] === undefined) {
        params['v'] = API_VERSION;
    }

    VKConnect.send('VKWebAppCallAPIMethod', {
        'method': method,
        'params': params,
        'request_id': requestId
    });
}

function getNewRequestId() {
    return (Math.floor(Math.random() * Number.MAX_SAFE_INTEGER)).toString();
}



